<template>
  <van-list finished-text="没有更多了">
    <van-cell
      v-for="item in list"
      :key="item.name"
      :title="item.name"
      @click="handleGoToPoint(item)"
    />
  </van-list>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      require: true,
      default: () => []
    }
  },
  methods: {
    handleGoToPoint(item) {
      this.$emit('goToPoint', item)
    }
  }
}
</script>

<style></style>
