<template>
  <div class="home-container">
    <base-map @handleMapInit="handleMapInit" />
    <div class="city-choose-container">
      <div class="tags-container">
        <van-tag
          v-for="(item, index) in citys"
          :key="index"
          size="large"
          style="margin-left:8px;margin-top:8px"
          @click="handleChangeCity(item)"
          :type="item.checked ? 'primary' : 'default'"
          plain
          >{{ item.name }}</van-tag
        >
      </div>
      <div class="list-container">
        <point-list :list="redPointList" @goToPoint="handleGoToPoint" />
      </div>
    </div>
  </div>
</template>

<script>
import BaseMap from '@/components/map'
import redPoints from '../../assets/data/redPoints.json'
import PointList from '../pointList'
export default {
  components: {
    BaseMap,
    PointList
  },
  data() {
    return {
      map: {},
      showPopup: true,
      redPointList: [],
      citys: [
        {
          name: '西安市',
          sw: [107.5, 33.666667],
          ne: [110, 34.833333],
          checked: true
        },
        {
          name: '铜川市',
          sw: [108.5, 34.666667],
          ne: [109.5, 35.666667],
          checked: false
        },
        {
          name: '宝鸡市',
          sw: [106, 33.333333],
          ne: [108.25, 35.3333333],
          checked: false
        },
        {
          name: '咸阳市',
          sw: [107.5, 34],
          ne: [109.5, 35.666667],
          checked: false
        },
        {
          name: '渭南市',
          sw: [108.75, 34],
          ne: [110.75, 36],
          checked: false
        },
        {
          name: '延安市',
          sw: [107.5, 35.166667],
          ne: [110.75, 37.666667],
          checked: false
        },
        {
          name: '汉中市',
          sw: [105, 32],
          ne: [108.5, 34],
          checked: false
        },

        {
          name: '榆林市',
          sw: [107, 36.83],
          ne: [111.5, 39.666667],
          checked: false
        },

        {
          name: '安康市',
          sw: [107.75, 31.5],
          ne: [110.5, 34],
          checked: false
        },
        {
          name: '商洛市',
          sw: [108.5, 33],
          ne: [111.5, 34.5],
          checked: false
        }
      ],
      city: '西安市'
    }
  },
  mounted() {
    const list = redPoints.filter((item) => item.city === this.city)
    this.redPointList = list
  },
  methods: {
    handleMapInit(map) {
      this.map = map
      this.handleDrawRedPoint(map)
      // this.handleConvert()
    },
    handleChangeCity(item) {
      // 改变tags 选中状态
      this.citys.forEach((city) => {
        if (city.name === item.name) {
          city.checked = true
        } else {
          city.checked = false
        }
      })
      // 地图缩放到选中地市的范围
      const map = this.map
      map.setCity(item.name)
      this.map.setPitch(0)
      // 列表切换到该地市的数据
      const list = redPoints.filter((point) => point.city === item.name)
      this.redPointList = list
    },
    handleGoToPoint(item) {
      // 改变tags 选中状态
      this.citys.forEach((city) => {
        if (city.name === item.city) {
          city.checked = true
        } else {
          city.checked = false
        }
      })
      // 地图定位到该点
      this.map.setZoomAndCenter(16, [item.lgtd, item.lttd])
      this.map.setPitch(70)
    },
    handleDrawRedPoint(map) {
      const AMap = window.AMap
      const markers = []
      redPoints.forEach((item) => {
        const icon = {
          // 图标类型，现阶段只支持 image 类型
          type: 'image',
          // 图片 url
          image: require('../../assets/img/flag.svg'),
          // 图片尺寸
          size: [30, 30]
          // 图片相对 position 的锚点，默认为 bottom-center
          // anchor: 'center'
        }
        const text = {
          // 要展示的文字内容
          content: item.name,
          // 文字方向，有 icon 时为围绕文字的方向，没有 icon 时，则为相对 position 的位置
          // direction: 'right',
          // 在 direction 基础上的偏移量
          offset: [0, 0],
          // 文字样式
          style: {
            // 字体大小
            fontSize: 12,
            // 字体颜色
            fillColor: 'red',
            // 描边颜色
            strokeColor: '#fff',
            // 描边宽度
            strokeWidth: 0
          }
          // zooms: [9, 20]
        }
        const labelMarker = new AMap.LabelMarker({
          name: '标注2', // 此属性非绘制文字内容，仅最为标识使用
          position: [item.lgtd, item.lttd],
          zIndex: 16,
          // 将第一步创建的 icon 对象传给 icon 属性
          icon: icon,
          // 将第二步创建的 text 对象传给 text 属性
          text: text
        })
        labelMarker.on('click', () => {
          this.handleGoToPoint(item)
        })
        markers.push(labelMarker)
        // const marker = new AMap.Marker({
        //   position: new AMap.LngLat(item.lgtd, item.lttd),
        //   icon: require('../../assets/img/flag.svg'),
        //   label: {
        //     direction: 'top',
        //     content: item.name,
        //   }
        // })
        // map.add(marker)
      })
      const labelsLayer = new AMap.LabelsLayer({
        zooms: [3, 20],
        zIndex: 1000,
        // 该层内标注是否避让
        collision: true,
        // 设置 allowCollision：true，可以让标注避让用户的标注
        allowCollision: true
      })
      labelsLayer.add(markers)
      map.add(labelsLayer)
    }
  }
}
</script>

<style>
.home-container {
  height: 100%;
}
.amap-marker-label {
  width: 120px;
  font-size: 12px;
  line-height: 18px;
  text-align: center;
  word-wrap: break-word;
  word-break: break-all;
  height: 18px;
  color: red;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0);
  border: none;
}
.city-choose-container {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 30%;
  z-index: 999;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}
.tags-container {
  margin-bottom: 8px;
}
.list-container {
  overflow-y: auto;
}
</style>
